@import '../style/variables';

.fact {
  max-width: 500px;
  padding: 0 1rem;
  display: flex;
  gap: 15px;
  align-items: center;
  font-size: 1.5rem;
}

.fact_button {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  border: none;
  cursor: pointer;
  background: none;
}

.fact_icon {
  width: 40px;
  height: 40px;
}

@media (min-width: 1281px) {
  .container {
    font-size: 2rem;
    width: 800px;
  }
}